import React, { Component } from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import MainMenuItem from './main-menu-item';

export default class MainMenu extends Component {

  constructor(props) {
    super(props);
  }

  createMenu() {
    let menuList = [
      {label: "", className: "team-setting"},
      {label: "", className: "timer"},
      {label: "", className: "uploads"},
      {label: "", className: "puzzle-settings"},
      {label: "  ", className: "point-reward"},
      {label: "", className: "result"},
      {label: "", className: "mapping-points"},
      {label: " ", className: "post-info"},
      {label: " ", className: "admin-passwords"},
      {label: "", className: "reset"},
    ];

    var tagList = [];
    _.forEach(menuList, (menuItem) => {
      tagList.push(
        <MainMenuItem className={menuItem.className} label={menuItem.label} key={menuItem.className}></MainMenuItem>
      );
    });
    return tagList;
  }

  render() {
    return (
      <ul className="main-menu list-unstyled">
        {this.createMenu()}
      </ul>
    );
  }
}